.effective{
  &__wrap{
    padding: 10em 0;
    width: 1840px;
    max-width: calc(100% - 5em);
    margin: 0 auto;
    background-color: $white;
    clip-path: polygon( 0% 4.332%,0% 4.332%,0.043% 3.629%,0.166% 2.963%,0.364% 2.341%,0.629% 1.774%,0.955% 1.269%,1.335% 0.836%,1.762% 0.484%,2.23% 0.221%,2.732% 0.057%,3.261% 0%,60.87% 0%,60.87% 0%,61.31% 0.047%,61.728% 0.184%,62.118% 0.403%,62.474% 0.697%,62.791% 1.057%,63.063% 1.478%,63.284% 1.951%,63.448% 2.469%,63.551% 3.025%,63.587% 3.61%,63.587% 3.61%,63.623% 4.196%,63.726% 4.751%,63.89% 5.269%,64.111% 5.742%,64.383% 6.163%,64.7% 6.524%,65.056% 6.817%,65.446% 7.036%,65.864% 7.173%,66.304% 7.22%,96.739% 7.22%,96.739% 7.22%,97.268% 7.277%,97.77% 7.441%,98.238% 7.704%,98.665% 8.056%,99.045% 8.489%,99.371% 8.994%,99.636% 9.561%,99.834% 10.183%,99.957% 10.85%,100% 11.552%,100% 92.78%,100% 95.668%,100% 95.668%,99.957% 96.371%,99.834% 97.037%,99.636% 97.659%,99.371% 98.226%,99.045% 98.731%,98.665% 99.164%,98.238% 99.516%,97.77% 99.779%,97.268% 99.943%,96.739% 100%,39.13% 100%,39.13% 100%,38.69% 99.953%,38.272% 99.816%,37.882% 99.597%,37.526% 99.303%,37.209% 98.943%,36.937% 98.522%,36.716% 98.049%,36.552% 97.531%,36.449% 96.975%,36.413% 96.39%,36.413% 96.39%,36.377% 95.804%,36.275% 95.249%,36.11% 94.731%,35.889% 94.258%,35.617% 93.837%,35.301% 93.476%,34.944% 93.183%,34.555% 92.964%,34.136% 92.827%,33.696% 92.78%,3.261% 92.78%,3.261% 92.78%,2.732% 92.723%,2.23% 92.559%,1.762% 92.296%,1.335% 91.944%,0.955% 91.511%,0.629% 91.006%,0.364% 90.439%,0.166% 89.817%,0.043% 89.15%,0% 88.448%,0% 7.22%,0% 4.332% );
  }
  &__desc{
    width: 61em;
    max-width: 100%;
    margin: 2.5em auto 0;
    text-align: center;
  }
  &__content{
    position: relative;
    margin-top: 6.25em;
    @media screen and (min-width: 993px){
      min-height: 31.75em;
      display: flex;
      align-items: center;
    }
  }
  &__logo{
    display: block;
    margin: 0 auto;
    width: 40em;
    max-width: 100%;
  }
  &__bottom{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 7.5em auto 0;
    width: 54.375em;
    max-width: 100%;
  }
  &__link{
    margin-top: 2.5em;
  }
}

.effective-item{
  display: flex;
  align-items: center;
  padding: .5em 1.25em;
  min-height: 5em;
  backdrop-filter: blur(20px);
  background: rgba(246, 247, 251, 0.5);
  border-radius: .625em;
  img{
    width: 2.5em;
    margin-right: 1.25em;
  }
  @media screen and (min-width: 993px){
    position: absolute;
    &:nth-child(1){
      top: -2.5em;
      left: 50%;
      transform: translateX(-50%);
    }
    &:nth-child(2){
      right: 11.875em;
      top: 0;
    }
    &:nth-child(3){
      right: -1.875em;
      top: 7.5em;
    }
    &:nth-child(4){
      right: 6.25em;
      top: 15em;
    }
    &:nth-child(5){
      right: 0;
      top: 22.5em;
    }
    &:nth-child(6){
      right: 28.125em;
      bottom: -2.875em;
    }
    &:nth-child(7){
      left: 18.125em;
      bottom: -3.125em;
    }
    &:nth-child(8){
      left: -2.5em;
      bottom: 3.75em;
    }
    &:nth-child(9){
      left: 6.875em;
      bottom: 11.25em;
    }
    &:nth-child(10){
      left: .875em;
      bottom: 19.125em;
    }
    &:nth-child(11){
      left: 10.25em;
      top: 0;
    }
  }
}

.advantages{
  margin-top: -10em;
  position: relative;
  &__bg{
    max-width: 100%;
    pointer-events: none;
    z-index: -1;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &__items{
    @include grid100gap(3.75em);
  }
}

.advantage-item{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 1em 2.5em;
  align-items: center;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 3.75em;
  }
  &__nav{
    margin-top: 2.75em;
  }
  &__link{
    padding: 0 2.875em;
  }
  @media screen and (min-width: 993px){
    &:nth-child(even) {
      .advantage-item__col {
        order: 2;
      }
    }
  }
  @media screen and (min-width: 1620px){
    .advantage-item__img{
      display: flex;
      justify-content: flex-end;
      img{
        width: calc(50vw - 3.5em);
      }
    }
    &:nth-child(even){
      .advantage-item__img{
        justify-content: unset;
      }
    }
  }

  @media screen and (min-width: 1920px){
    .advantage-item__img{
      img{
        width: calc(1840px / 2 - 1.25em);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .effective{
    &__wrap{
      padding: 2em 0;
      clip-path: unset;
      max-width: 100%;
      border-radius: 1.25em;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__content{
      margin-top: 3.125em;
    }
    &__logo{
      display: none;
    }
    &__items{
      margin-top: 2em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__bottom{
      margin-top: 3em;
    }
  }

  .advantages{
    margin-top: 0;
    &__items{
      gap: 2em;
    }
  }

  .advantage-item{
    grid-template-columns: 100%;
    &__text,
    &__nav{
      margin-top: 1em;
    }
    &__link{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .effective{
    &__items{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .effective-item{
    z-index: .75em;
  }
}
