.main{
  &__wrap{
    padding: 5em 0 1.75em;
    width: 1840px;
    max-width: calc(100% - 5em);
    margin: 0 auto;
    background-color: $white;
    clip-path: polygon( 100% 5.319%,100% 5.319%,99.957% 4.456%,99.834% 3.638%,99.636% 2.875%,99.371% 2.178%,99.045% 1.558%,98.665% 1.026%,98.238% 0.594%,97.77% 0.271%,97.268% 0.07%,96.739% 0%,3.261% 0%,3.261% 0%,2.732% 0.07%,2.23% 0.271%,1.762% 0.594%,1.335% 1.026%,0.955% 1.558%,0.629% 2.178%,0.364% 2.875%,0.166% 3.638%,0.043% 4.456%,0% 5.319%,0% 91.135%,0% 94.681%,0% 94.681%,0.043% 95.544%,0.166% 96.362%,0.364% 97.125%,0.629% 97.822%,0.955% 98.442%,1.335% 98.974%,1.762% 99.406%,2.23% 99.729%,2.732% 99.93%,3.261% 100%,60.87% 100%,60.87% 100%,61.31% 99.942%,61.728% 99.774%,62.118% 99.505%,62.474% 99.145%,62.791% 98.702%,63.063% 98.185%,63.284% 97.604%,63.448% 96.968%,63.551% 96.286%,63.587% 95.567%,63.587% 95.567%,63.623% 94.848%,63.726% 94.166%,63.89% 93.53%,64.111% 92.95%,64.383% 92.433%,64.7% 91.99%,65.056% 91.63%,65.446% 91.361%,65.864% 91.193%,66.304% 91.135%,96.739% 91.135%,96.739% 91.135%,97.268% 91.065%,97.77% 90.864%,98.238% 90.541%,98.665% 90.109%,99.045% 89.577%,99.371% 88.957%,99.636% 88.26%,99.834% 87.497%,99.957% 86.678%,100% 85.816%,100% 5.319% );
    background-image: url(../img/main-bg.jpg);
    background-size: cover;
    background-position: center;
  }
  &__desc{
    margin: 1.25em auto 0;
    width: 67.5em;
    max-width: 100%;
    text-align: center;
  }
  &__items{
    margin-top: 3.75em;
    @include center;
  }
  &__nav{
    margin-top: 3.75em;
    position: relative;
    z-index: 2;
  }
  &__img{
    margin: -2.75em auto 0;
    width: 76em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.main-item{
  margin: 0 1.875em;
  display: flex;
  align-items: center;
  img{
    width: 2.5em;
    margin-right: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__wrap{
      padding: 2em 0;
      clip-path: unset;
      max-width: 100%;
      border-radius: 1.25em;
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 2em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__link{
      width: 100%;
    }
    &__img{
      margin-top: 1em;
    }
  }

  .main-item{
    margin: 0;
    img{
      width: 1.875em;
      margin-right: .75em;
    }
    .text22{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
    .h1{
      font-size: 1.75em;
    }
  }
}
