.m-section{
  padding: 5em 0;
  &._first{
    padding-top: 0;
  }
  &._last{
    padding-bottom: 0;
  }
}

.alternative{
  &__top{
    @include grid100gap(2.5em);
    text-align: center;
  }
  &__desc,
  &__desc2{
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    width: 68em;
  }
  &__desc2{
    width: 71.625em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(12,calc(100% / 12 - 1.25em * 11 / 12));
    gap: 1.25em;
  }
}

.alternative-card{
  grid-column: span 3;
  padding: 1.25em 1.25em 1.875em;
  border: 1px solid #e6edff;
  background-color: $white;
  border-radius: .75em;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 4;
      .rect{
        width: 19.125em;
      }
      .rect1{
        left: -10em;
      }
      .rect2{
        right: -10em;
      }
      .rect3{
        right: -10em;
      }
      .rect4{
        left: -10em;
      }
    }
  }
  .rect{
    border: 1px solid #e4eaff;
    border-radius: .75em;
    position: absolute;
    width: 11em;
    height: 7.75em;
  }
  .rect1{
    background: linear-gradient(270.00deg, rgba(202, 233, 255, 0.1) -1.541%,rgba(202, 233, 255, 0.1) 101.541%);
    top: -3.25em;
    left: -6em;
  }
  .rect2{
    background: linear-gradient(270.00deg, rgba(202, 233, 255, 0.1) -1.541%,rgba(202, 233, 255, 0.1) 101.541%);
    top: -3.25em;
    right: -6em;
  }
  .rect3{
    background: linear-gradient(270.00deg, rgba(202, 233, 255, 0.1) -0.017%,rgba(202, 233, 255, 0.1) 100.017%);
    top: 5.5em;
    right: -6em;
    height: 20.5em;
  }
  .rect4{
    background: linear-gradient(270.00deg, rgba(202, 233, 255, 0.1) -0.017%,rgba(202, 233, 255, 0.1) 100.017%);
    top: 5.5em;
    left: -6em;
    height: 20.5em;
  }
  &__img,
  &__text{
    position: relative;
    z-index: 2;
  }
  &__img{
    height: 11.875em;
    img{
      height: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2em 0;
  }

  .alternative{
    &__top{
      gap: 1.25em;
    }
    &__grid{
      grid-template-columns: repeat(6,calc(100% / 6 - .75em * 5 / 6));
      gap: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .alternative{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
  }

  .alternative-card{
    font-size: .75em;
    grid-column: span 2;
  }
}
