.solution{
  &__grid{
    margin-top: 3.75em;
    position: relative;
  }
  &__items{
    @include grid100gap(.625em);
    width: calc(50% - .625em);
    margin-left: auto;
  }
  &__nav{
    margin-top: 3.75em;
  }
}

.solution-item{
  @media screen and (min-width: 993px){
    &._active{
      .solution-item__btn{
        opacity: 1;
        background-color: $white;
      }
      .solution-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      display: block !important;
      transform: .3s;
      pointer-events: none;
      opacity: 0;
      position: absolute;
      width: calc(50% - .625em);
      top: 0;
      left: 0;
    }
    &__btn{
      opacity: .5;
      &:hover{
        opacity: 1;
      }
    }
  }
  &__btn{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 2.5em;
    min-height: 5em;
    border-radius: 1.25em;
    img{
      width: 2.5em;
      margin-right: 1.25em;
    }
  }
  &__img{
    @include imgCover(57.9%);
    border-radius: 2.5em;
  }
}

.slider{
  position: relative;
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      padding-right: 1em;
    }
  }
}

.solution-slider{
  border-radius: 2.5em;
  .swiper{
    &-button{
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
}

.swiper-button{
  width: 3.75em;
  height: 3.75em;
  border-radius: 50%;
  img{
    width: 1.75em;
  }
}


@media screen and (max-width: 992px){
  .solution{
    &__nav,
    &__grid{
      margin-top: 2em;
    }
    &__items{
      width: 100%;
      gap: 1em;
    }
    &__link{
      width: 100%;
    }
  }

  .solution-item{
    &._active2{
      .solution-item__btn:after{
        transform: rotate(-180deg);
      }
    }
    &__btn{
      background-color: $white;
      padding: 0 2em;
      &:after{
        content: '';
        width: 2em;
        height: 2em;
        flex-shrink: 0;
        margin-left: auto;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        transition: all .3s;
      }
    }
    &__hidden{
      margin-top: 1em;
      display: none;
    }
    &__img{
      border-radius: 1.25em;
    }
  }

  .solution-slider{
    border-radius: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .solution-item{
    &__btn{
      font-size: .75em;
      text-align: left;
      span{
        padding-right: .25em;
        line-height: 120%;
      }
    }
  }

  .swiper-button{
    font-size: .5em;
  }

  .solution-slider{
    .swiper-button{

    }
  }
}
