.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: rgba($black,.8);
    background-image: url(../img/modal-bg.svg);
    background-size: cover;
    background-position: center;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #f6f7fb;
        width: 47.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 6.25em 5em;
        border-radius: 3.75em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.modal-form{
    margin-top: 2.5em;
    &__fields{
        @include grid100gap(.625em);
    }
    &__btn{
        width: 100%;
        margin-top: .625em;
    }
}

.thank-modal{
    &__icon{
        width: 12.5em;
        display: block;
        margin: 0 auto 1.25em;
    }
    &__title{
        text-align: center;
    }
    &__desc{
        text-align: center;
        color: #54555f;
        margin-top: 1.25em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2em 1em;
            border-radius: 1em;
            width: 35em;
        }
        &__title{
            font-size: 1.5em;
        }
    }

    .modal-form{
        font-size: .875em;
        margin-top: 1.5em;
        &__btn{
            font-size: 1.21em;
        }
    }

    .thank-modal{
        &__icon{
            width: 8em;
            margin-bottom: 1em;
        }
        &__desc{
            margin-top: 1em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
