.footer{
  padding: 3.75em 0 2.75em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 11.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    width: 22em;
    max-width: 100%;
    color: #54555F;
    margin-top: 2.5em;
  }
  &__bottom{
    margin-top: 2.5em;
    display: flex;
  }
  &__link,
  &__info{
    font-weight: 500;
    font-size: 1em;
    line-height: 110%;
    color: rgba($black,.25);
  }
  &__link{
    margin-right: 5em;
  }
  &__info{
    margin-left: auto;
  }
}

.footer-nav{
  display: flex;
  align-items: flex-start;
  &__list{
    font-weight: 500;
    margin-right: 3em;
    @include grid100gap(1.25em);
    &:last-child{
      margin: 0;
    }
    a{
      font-size: 1.125em;
      line-height: 110%;
    }
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__phone{
    font-weight: 500;
  }
  &__email{
    margin-top: .83em;
  }
  .socials{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__top{
      @include grid100gap(2em);
    }
    &__desc{
      width: 100%;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin: 0 0 1em;
    }
    &__info{
      margin: 0;
    }
  }

  .footer-nav{
    justify-content: space-between;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo{
      width: 9em;
    }
    &__desc{
      margin-top: 1em;
      .text22{
        font-size: 1em;
      }
    }
  }

  .footer-nav{
    @include grid100gap(1em);
    &__list{
      gap: 1em;
      a{
        font-size: 1em;
      }
    }
  }

  .footer-contacts{
    &__phone{
      font-size: 1.75em;
    }
    &__email{
      font-size: 1.25em;
    }
    .socials{
      margin-top: 1.5em;
    }
  }
}
