.header{
  padding: .625em 0;
  position: relative;
  z-index: 10;
  &__wrap{
    margin: 0 auto;
    width: 1840px;
    max-width: calc(100% - 5em);
    background-color: $white;
    border-radius: 31.25em;
  }
  &__inner{
    padding: 1.25em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (min-width: 993px){
      position: relative;
      &:after{
        content: '';
        width: 10em;
      }
      .header-user{
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6.6875em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.nav{
  font-weight: 500;
  ul{
    display: flex;
    align-items: center;
  }
  li{
    margin-right: 2.5em;
    &:last-child{
      margin: 0;
    }
  }
  a{
    font-size: 1.125em;
    line-height: 110%;
  }
}

.header-user{
  position: relative;
  &__btn{
    width: 10em;
    height: 3.25em;
    padding: 0;
    font-weight: 500;
    font-family: $font1;
    span{
      font-size: 1.125em;
    }
  }
  &._show-modal{
    .auth{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.burger{
  display: none;
}

.auth{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: .625em;
  &__dialog{
    width: 32.5em;
    background: #f6f7fb;
    padding: 3.75em;
    border-radius: 3.75em;
  }
  &__bg{
    display: none;
  }
}

.auth-form{
  display: none;
  &._active{
    display: block;
  }
  &__fields{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__btn{
    width: 100%;
    margin-top: .625em;
  }
  &__btn2{
    margin: 1.25em auto 0;
    width: 100%;
    @include center;
    font-weight: 500;
    span{
      font-size: 1.5em;
      line-height: 110%;
    }
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 0;
    &__wrap{
      border-radius: 0;
      max-width: 100%;
    }
    &__inner {
      padding: .625em 0;
    }
    &__logo{
      width: 5em;
      z-index: 11;
      position: relative;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    svg,
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 5em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
  }

  .nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 1em;
    }
  }

  .header-user{
    margin-top: 2em;
    &._show-modal{
      .auth{
        &__bg{
          display: block;
        }
      }
    }
  }

  .auth{
    &__dialog{
      max-width: calc(100% - 1em);
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
}


@media screen and (max-width: 576px){
  .auth{
    &__dialog{
      padding: 1em;
      border-radius: 1em;
    }
  }

  .auth-form{
    font-size: .875em;
    &__btn{
      font-size: 1.21em;
    }
  }
}
