@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 4.375em;
    height: 5em;
    border-radius: 31.25em;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.375em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: #01a4ff;
        color: $white
    }
    &:active{
        background-color: #0079bd;
        color: $white
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white
    }
    &:active{
        background-color: #0079bd;
        color: $white
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 3em;
        height: 4em;
        span{
            font-size: 1em;
        }
    }
}


