.faq{
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    pointer-events: none;
    position: absolute;
    right: 0;
    top: -45em;
    width: 105.625em;
    max-width: 100%;
  }
  &__title{
    max-width: 13em;
    margin: 0 auto;
    text-align: center;
  }
  &__items{
    margin-top: 3.75em;
    @include grid100gap(.625em);
  }
  &__nav{
    margin-top: 3.75em;
  }
}

.qa-card{
  background-color: $white;
  border-radius: 3.75em;
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding: .625em .625em .625em 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 500;
    font-size: 1.5em;
    line-height: 110%;
  }
  &__btn{
    margin-left: 1em;
    @include center;
    width: 5.375em;
    height: 5.375em;
    border: 1px solid #e2e6f3;
    border-radius: 50%;
    flex-shrink: 0;
    &:hover{
      background: #e2e6f3;
      path{
        fill: $white;
      }
    }
    svg{
      width: 46%;
    }
  }
  &__hidden{
    padding: 2.5em;
    display: none;
    border-top: 1px solid #f6f7fb;
  }
}

.edu{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__desc{
    margin-top: 2.5em;
  }
  .create__list{
    margin-top: 1.25em;
    gap: .625em;
  }
  &__hint{
    margin-top: 3.75em;
    text-align: center;
  }
  &__nav{
    margin-top: 2.5em;
  }
}

.m-video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    @include center;
    width: 7.5em;
    height: 7.5em;
    border-radius: 50%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 2.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .faq{
    &__bg{
      display: none;
    }
    &__nav,
    &__items{
      margin-top: 2em;
    }
    &__link{
      width: 100%;
    }
  }

  .qa-card{
    border-radius: 1em;
    &__show{
      font-size: .75em;
    }
    &__hidden{
      padding: 2em;
    }
    &__btn{
      width: 4em;
      height: 4em;
    }
  }

  .edu{
    &__grid{
      grid-template-columns: 100%;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav,
    &__hint{
      margin-top: 2em;
    }
    &__link{
      width: 100%;
    }
  }

  .m-video{
    &__play{
      width: 5em;
      height: 5em;
    }
    &__img{
      img{
        border-radius: 1.25em;
      }
    }
  }
}


