.create{
  position: relative;
  &__bg{
    pointer-events: none;
    z-index: -1;
    position: absolute;
    left: 0;
    top: -96em;
    width: 120em;
    max-width: 100%;
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 46.375em 39.375em;
    justify-content: space-between;
    gap: 2em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-weight: 700;
    font-size: 5.8125em;
    line-height: 110%;
    span{
      color: $white;
    }
  }
  &__desc{
    margin-top: auto;
    padding-top: 1em;
  }
  &__list{
    margin-top: 3.75em;
    @include grid100gap(1.25em);
    li{
      min-height: 2.5em;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
        width: 2.5em;
        height: 2.5em;
        flex-shrink: 0;
        margin-right: 1.25em;
      }
    }
  }
}

.team{
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 20.875em calc(100% - 20.875em - 3.5em);
    gap: 1.5em 3.5em;
    margin-top: 3.75em;
  }
  &__col{
    position: relative;
    z-index: 2;
  }
}

.team-slider{
  position: relative;
  overflow: unset;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
      width: 24.375em;
      @media screen and (min-width: 993px){
        opacity: 0;
        pointer-events: unset;
        transition: all .3s;
        &-active,
        &-next,
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
        &-next+.swiper-slide~.swiper-slide{
          opacity: .2;
          pointer-events: unset;
        }
      }
    }
    &-nav{
      z-index: 2;
      position: absolute;
      top: 9.6875em;
      left: 0;
      transform: translateX(-50%);
    }
    &-button{
      position: static;
      margin: 0 0 .625em;
    }
  }
}

