    @include imgCover(119%);
    border-radius: 3.75em;
  }
  &__content{
    @include grid100gap(.625em);
    text-align: center;
    margin-top: 1.25em;
  }
  &__prof{
    font-size: 1em;
    line-height: 110%;
    opacity: 0.25;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .create{
    &__bg{
      display: none;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 1.875em;
      span{
        color: inherit;
      }
    }
    &__list{
      font-size: .75em;
      margin-top: 2em;
      li:before{
        width: 1.75em;
        height: 1.75em;
        margin-right: 1em;
      }
    }
  }

  .team{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2em;
    }
  }

  .team-slider{
    .swiper{
      &-slide{
        width: 20em;
        padding-right: 1em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .team-card{
    &__img{
      border-radius: 1em;
    }
  }
}


