.scheme {
  position: relative;
  .container {
    position: relative;
    z-index: 2;
  }
  &__inner {
    max-width: 80em;
    margin: 0 auto;
  }
  &__title {
    text-align: center;
    span {
      color: #2C7BE5;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 3.75em;
    margin-top: 3.75em;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 3.75em;
    &-desc {
      width: 48%;
    }
    &-img {
      width: 52%;
      img {
        width: 100%;
        border-radius: 1.25em;
      }
    }
    &-nav {
      display: inline-block;
      background: rgb(255, 255, 255);
      box-shadow: 0 0 0 0.0625em rgba(14, 9, 72, 0.08),0 0.0625em 0.125em 0 rgba(14, 9, 72, 0.08),0 0.125em 0.375em 0 rgba(18, 7, 143, 0.08);
      border-radius: 0.375em;
      padding: 0.25em 1em;
      font-weight: 500;
      span {
        color: #2C7BE5;
      }
    }
    &-title {
      margin-top: 2.5em;
      span {
        font-size: 2.5em;
        line-height: 110%;
        font-weight: 700;
      }
    }
    &-text {
      margin-top: 2.5em;
    }
  }
  &__img {
    position: absolute;
    top: -36em;
    right: 0;
    max-width: 88.125em;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
}
.integrum {
  position: relative;
  .container {
    position: relative;
    z-index: 2;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    text-align: center;
  }
  &__text {
    max-width: 67.8125em;
    text-align: center;
    margin: 3.75em auto 0;
  }
  &__cards {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    margin-top: 2.5em;
  }
  &__btn {
    padding: 0 2.875em;
    margin-top: 2.5em;
  }
  &__img {
    position: absolute;
    top: -40em;
    left: 0;
    width: 100%;
    max-width: 87.1875em;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
}
.integrum-card {
  position: relative;
  background: #fff;
  border-radius: 1.25em;
  padding: 2.5em;
  &__icon {
    max-width: 4.625em;
    height: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
    }
  }
  &__number {
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    span {
      color: rgb(225, 225, 225);
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 2.5em;
    span {
      display: block;
    }
  }
}
