  &__title {
    text-align: center;
  }
  &__text {
    max-width: 52.8125em;
    text-align: center;
    margin: 3.75em auto 0;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 3.75em / 4));
    gap: 1.25em;
    margin-top: 2.5em;
  }
}
.tariff-card {
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #F6F7FB;
  border-radius: 2.5em;
  padding: 2.5em 1.25em 1.25em;
  &__title {
    min-height: 7.5em;
    &-row1 {
      font-size: 2.25em;
      line-height: 110%;
      font-weight: 500;
    }
    &-row2 {
      display: block;
      font-size: 1.5em;
      line-height: 110%;
      font-weight: 500;
      color: #54555F;
      margin-top: 0.3125em;
    }
  }
  &__price {
    border-bottom: 0.0625em solid #D2D5E2;
    padding-bottom: 2.5em;
    margin-top: 1.25em;
  }
  &__text {
    margin-top: 1.25em;
    margin-bottom: auto;
  }
  &__btn {
    width: 100%;
    padding: 0 1em;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px) {
  .scheme {
    &__items {
      gap: 1.875em;
      margin-top: 1.875em;
    }
    &__item {
      gap: 2em;
      &-title {
        margin-top: 1.25em;
        span {
          font-size: 1.5em;
        }
      }
      &-text {
        margin-top: 1.25em;
        p {
          font-size: 1em;
        }
      }
    }
    &__img {
      top: -24em;
      max-width: 60em;
    }
  }
  .integrum {
    &__text {
      margin-top: 1.875em;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      margin-top: 1.875em;
    }
    &__btn {
      margin-top: 1.875em;
    }
    &__img{
      display: none;
    }
  }
  .integrum-card {
    padding: 1.25em;
    &__text {
      margin-top: 1.25em;
      span {
        font-size: 1.125em;
      }
      p {
        font-size: 1em;
      }
    }
  }
  .tariffs {
    &__inner {
      max-width: 100%;
      background: #fff;
      padding: 2em 0;
      &:after {
        display: none;
      }
    }
    &__text {
      margin-top: 1.875em;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      margin-top: 1.875em;
    }
  }
  .tariff-card {
    &__title {
      min-height: 4.375em;
      p {
        font-size: 1.5em;
      }
      span {
        font-size: 1em;
      }
    }
    &__price {
      padding-bottom: 1.25em;
      p {
        font-size: 1.125em;
      }
    }
    &__text {
      p {
        font-size: 1em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .scheme {
    &__item {
      flex-direction: column;
      gap: 1.25em;
      &-desc {
        width: 100%;
      }
      &-img {
        width: 100%;
        order: -1;
      }
    }
    &__img {
      top: -10em;
    }
  }
  .integrum {
    &__cards {
      grid-template-columns: 100%;
    }
    &__btn {
      padding: 0 1em;
    }
  }
  .tariffs {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .tariff-card {
    &__title {
      min-height: unset;
    }
  }
}
