      background-position: center;
      z-index: -1;
    }
  }
  &__bg{
    position: absolute;
    left: 0;
    top: 0;
    width: 86.4375em;
    border-radius: 3.75em;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;
    width: 63.875em;
  }
  &__text,
  &__btn{
    position: relative;
    z-index: 5;
  }
  &__text{
    text-align: center;
    width: 61.75em;
    max-width: 100%;
    @include grid100gap(3.75em);
  }
  &__btn{
    margin-top: 2.625em;
  }
}

.demo2{
  .demo__text{
    width: 64.5em;
    .text36{
      max-width: 25em;
      margin: 0 auto;
    }
  }
}

.results{
  &__wrap{
    margin-top: 3.75em;
    background-color: $white;
    border-radius: 3.75em;
    padding: 1.875em 1.875em 1.875em 5em;
    display: grid;
    grid-template-columns: calc(100% - 38.25em) 35.75em;
    gap: 2em 2.5em;
    align-items: center;
  }
  &__subtitle{
    font-size: .73em;
    font-weight: 500;
    line-height: 110%;
    margin-bottom: .625em;
    opacity: .25;
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__link{
    width: 23.125em;
    max-width: 100%;
  }
}

.results-slider{
  &__img{
    @include imgCover(119%);
    border-radius: 1.875em;
    clip-path: polygon( 100% 4.688%,100% 4.688%,99.931% 3.927%,99.733% 3.206%,99.415% 2.533%,98.988% 1.919%,98.464% 1.373%,97.853% 0.904%,97.166% 0.523%,96.413% 0.239%,95.606% 0.061%,94.755% 0%,15.734% 0%,15.734% 0%,14.884% 0.061%,14.077% 0.239%,13.324% 0.523%,12.637% 0.904%,12.026% 1.373%,11.501% 1.919%,11.075% 2.533%,10.757% 3.206%,10.558% 3.927%,10.49% 4.688%,10.49% 17.188%,10.49% 17.188%,10.421% 17.948%,10.222% 18.669%,9.904% 19.342%,9.478% 19.956%,8.953% 20.502%,8.342% 20.971%,7.655% 21.352%,6.903% 21.636%,6.095% 21.814%,5.245% 21.875%,5.245% 21.875%,4.394% 21.936%,3.587% 22.114%,2.834% 22.398%,2.147% 22.779%,1.536% 23.248%,1.012% 23.794%,0.585% 24.408%,0.267% 25.081%,0.069% 25.802%,0% 26.563%,0% 95.313%,0% 95.313%,0.069% 96.073%,0.267% 96.794%,0.585% 97.467%,1.012% 98.081%,1.536% 98.627%,2.147% 99.096%,2.834% 99.477%,3.587% 99.761%,4.394% 99.939%,5.245% 100%,10.49% 100%,94.755% 100%,94.755% 100%,95.606% 99.939%,96.413% 99.761%,97.166% 99.477%,97.853% 99.096%,98.464% 98.627%,98.988% 98.081%,99.415% 97.467%,99.733% 96.794%,99.931% 96.073%,100% 95.313%,100% 4.688% );
  }
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
      position: absolute;
      right: 1.25em;
      bottom: 1.25em;
    }
    &-button{
      position: static;
      margin: 0 0 0 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .demo{
    &__inner{
      padding: 3em 1em;
      &:before{
        border-radius: 1.25em;
        clip-path: unset;
      }
    }
    &__bg,
    &__img{
      display: none;
    }
    &__text{
      gap: 1.25em;
    }
    &__link{
      margin-top: 1.25em;
      width: 100%;
    }
  }

  .results{
    &__wrap{
      margin-top: 1.5em;
      border-radius: 1em;
      padding: 1em;
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .results{
    &__link{
      width: 100%;
    }
  }

  .results-slider{
    &__img{
      border-radius: 1em;
    }
  }
}
